import tuiButton from "@/components/extend/button/button"
import tuiLoadmore from "@/components/loadmore/loadmore"
import tuiNomore from "@/components/nomore/nomore"
export default {
	components: {
		tuiButton,
		tuiLoadmore,
		tuiNomore
	},
	data() {
		return {
			loadding: false,
			pullUpOn: true
		}
	},
	methods: {
		getSignUrl: function(index) {
			let url = "";
			if (index === 1) {
				url = "/static/images/mall/coupon/img_couponcentre_received_3x.png"
			} else if (index == 3) {
				url = "/static/images/mall/coupon/img_coupon_beused_3x.png"
			} else if (index == 4) {
				url = "/static/images/mall/coupon/img_coupon_failure_3x.png"
			}
			return this.tui.webURL() + url
		}
	},
	onPullDownRefresh() {
		setTimeout(() => {
			this.pullUpOn = true
			uni.stopPullDownRefresh()
		}, 200);
	},
	onReachBottom() {
		//只是测试效果，逻辑以实际数据为准
		if (!this.pullUpOn) return;
		this.loadding = true
		setTimeout(() => {
			this.loadding = false
			this.pullUpOn = false
		}, 1000)
	}
}
